.landing {
    position: relative;
    background: url("./logo.jpg") no-repeat center center/cover;
    height: 100vh;
}
.landing-inner {
    color: #fff;
    height: 100%;
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.x-large {
    font-size: 3rem;
}
.lead {
    font-size: 1rem;
}
